}
input[type="radio"] {
  -webkit-appearance: none;
  appearance: none;
  display: inline-block;
  width: 20px;
  height: 20px;
  padding: 3px;
  background-clip: content-box;
  border: 2px solid black;
  background-color: transparent;
  border-radius: 50%;
}

input[type="radio"]:checked {
  background-color: var(--primary);
  border: 2px solid var(--primary);
}
.campus_shortImg,
.campus_longImg {
  width: 100%;
  height: 303px;
  border-radius: 8px;
  object-fit: center;
  transition: all ease-in-out 0.3s;
  cursor: pointer;
}
.campus_shortImg:hover {
  opacity: 0.4;
}
.modal-img-campus {
  width: 700px;
}
.campus_map {
  width: 100%;
  height: 360px;
}
.campus-dividerLine {
  border-top: 1px solid transparent;
}
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(15, 15, 15, 0.541);
  display: flex;
  align-items: center;
  z-index: 99;
}
.overlay img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0, 0, 0, 0.5);
}
.joinCampus{
  margin-left: 20px;
}
.overlay .dismiss {
  position: absolute;
  top: 10%;
  right: 20px;
  font-size: 30px;
  color: #ffffff;
  z-index: 999;
  cursor: pointer;
}
.image_wrapper {
  position: relative;
  transition: all ease-in-out 0.3s;
}
.expand_arrow {
  position: absolute;
  right: 8%;
  top: 5%;
  font-size: 2rem;
  cursor: pointer;
  z-index: -1;
  color: rgb(39, 38, 38);
  font-weight: 900;
  display: none;
  transition: all ease-in-out 0.3s;
}
.image_wrapper:hover .expand_arrow {
  display: block;
}
.campus_skeleton-img {
  animation: skeleton-loading 1s linear infinite alternate;
  height: 350px;
  width: 100%;
  border-radius: 8px;
}
.course-font {
  font-size: 18px;
  font-weight: 400;
}
.left-mar{
  margin-left: 32px;
}
